// --- ipad-retina -------------------------------------------------------------

@mixin ipad-retina($orientation: false) {
  $min: 768px;
  $max: 1024px;
  $pixel-ratio: 2;

  @include mq(
    $min-device-width: $min,
    $max-device-width: $max,
    $orientation: $orientation,
    $-webkit-device-pixel-ratio: $pixel-ratio
  ) {
    @content;
  }
}

// --- ipad-pro -------------------------------------------------------------

@mixin iPadPro {
  @media (device-width: 1024px) and (device-height: 1366px),
    (device-height: 1024px) and (device-width: 1366px),
    (device-width: 834px) and (device-height: 1194px),
    (device-height: 834px) and (device-width: 1194px) {
    @content;
  }
}
@mixin iPadPro_12_9 {
  @media (device-width: 1024px) and (device-height: 1366px),
    (device-height: 1024px) and (device-width: 1366px) {
    @content;
  }
}
@mixin iPadPro_11 {
  @media (device-width: 834px) and (device-height: 1194px),
    (device-height: 834px) and (device-width: 1194px) {
    @content;
  }
}

// --- orientation -------------------------------------------------------------

@mixin landscape() {
  @include mq($orientation: landscape) {
    @content;
  }
}

@mixin portrait() {
  @include mq($orientation: portrait) {
    @content;
  }
}

// --- other -------------------------------------------------------------

@mixin HOVER {
  cursor: pointer;
  @include min-screen(1025px) {
    &:hover {
      @content;
    }
  }
}
@mixin ANDR {
  @include max-screen(374px) {
    @content;
  }
}
@mixin Safari {
  @media screen and (-webkit-min-device-pixel-ratio: 0) {
    @content;
  }
}
@mixin FireFox {
  @-moz-document url-prefix() {
    @content;
  }
}
